// src/pages/reset.tsx

import React, { useState } from 'react';
import { supabase } from '../supabase';

export default function ResetItemData() {
  const [status, setStatus] = useState('');
  const [loading, setLoading] = useState(false);

  const reinjectItemData = async () => {
    setLoading(true);
    setStatus('');

    const initialData = [
      { item: '#2 pencils, sharpened', bulk: 103.96, carton: 576 },
      { item: 'Watercolor paints', bulk: 23.45, carton: 24 },
      { item: 'Earbud/headphones', bulk: 5.24, carton: 1 },
      { item: 'Crayons', bulk: 25.50, carton: 24 },
      { item: 'Scissors', bulk: 21.89, carton: 24 },
      { item: 'Large pink eraser', bulk: 9.84, carton: 100 },
      { item: 'Glue sticks', bulk: 15.99, carton: 30 },
      { item: 'Multi-colored markers', bulk: 11.04, carton: 12 },
      { item: 'Dry erase markers', bulk: 4.66, carton: 4 },
      { item: 'Wide ruled composition notebook', bulk: 22.76, carton: 12 },
      { item: 'Wide ruled paper', bulk: 3.99, carton: 1 },
      { item: 'White school glue', bulk: 3.99, carton: 1 },
      { item: '(2) pocket folders', bulk: 3.98, carton: 2 },
      { item: 'Three ring binder (1 ½ inch size)', bulk: 4.44, carton: 1 },
      { item: 'Clear Pencil box', bulk: 27.99, carton: 12 },
      { item: 'AA batteries', bulk: 12.98, carton: 4 },
      { item: 'Facial tissue', bulk: 20.99, carton: 12 },
      { item: 'Hand sanitizer', bulk: 4.90, carton: 1 },
      { item: 'Backpack 18”', bulk: 120.00, carton: 24 }
    ];

    const rows = initialData.map(item => ({
      name: item.item,
      bulk: item.bulk,
      carton: item.carton
    }));

    const { data, error } = await supabase
      .from('price_items')  // ✅ Corrected table name
      .upsert(rows );

    if (error) {
      console.error('❌ Supabase Error:', error.message);
      setStatus(`❌ Failed: ${error.message}`);
    } else {
      setStatus(`✅ Reinjected ${data?.length || 0} items`);
    }

    setLoading(false);
  };

  return (
    <div style={{ padding: 20, fontFamily: 'Arial' }}>
      <h2>🛠 Reset Item Data</h2>
      <button
        onClick={reinjectItemData}
        disabled={loading}
        style={{ padding: '10px 20px' }}
      >
        {loading ? 'Resetting...' : '🔁 Reset Item Table'}
      </button>
      {status && <p style={{ marginTop: 20 }}>{status}</p>}
    </div>
  );
}
